@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;1,200;1,300;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
h1,
h2,
h3,
h4,
h5,
h6,
p,
p1 {
  font-family: "Source Sans Pro", sans-serif;
}
.text {
  color: rgba(0, 0, 10, 0.85);
}
h1 {
  font-size: clamp(2rem, 2.5vw, 2.5rem);
}
h2 {
  font-size: clamp(1.5rem, 2vw, 2rem);
}
h3 {
  font-size: clamp(1.1rem, 1.8vw, 1.5rem);
}
h4 {
  font-size: clamp(1rem, 1.5vw, 1.3rem);
}
h5 {
  font-size: clamp(0.8rem, 1.3vw, 1.1rem);
}

p1 {
  font-size: clamp(1.1rem, 1.2vw, 1.3rem);
}
p {
  font-size: clamp(1.2rem, 1.2vw, 1.5rem);
}

.box-element {
  box-shadow: hsl(0, 0%, 80%) 0 0 4px;
  background-color: #fff;
  border-radius: 4px;
}

#App-grid{
  display: grid;
  grid-template-columns:1fr;
  grid-template-rows: minmax(67px,auto) minmax(calc(90vh - 70px),auto) 1fr;
  grid-template-areas:
  "header" "content" "footer";
}
header{
  grid-area: header;
}

footer{
  grid-area: footer;
  height: 20vh;
}

#content{
  min-height: 80vh;
  grid-area: content;
}
.content-container{
    margin: 0 10px;
}
.content-top{
    display:grid;
}

.content-top >div:nth-child(1){
    border-right: 2px solid hsl(0, 9%, 94%);
    border-collapse: collapse;
}

.content-bottom{
    border-top: 2px solid hsl(0, 6%, 94%);
    display:flex;
    justify-content:flex-end;
}

@media screen and (max-width:720px){
    .content-top {
        display: grid;
        grid-template-columns: 5fr 9fr;
    }
    .content-top>div:nth-child(3) {
         grid-area: 2 / 1 / 3 / 3;
         border-top: 2px solid hsl(0, 33%, 97%);
         padding-left: 1rem;
    }
}

@media screen and (min-width:720px){
    .content-container{
        margin:0 5%;
    }
    .content-top{
        grid-template-columns: 25% 45% 30%;
    }
    .content-top >div:nth-child(3){
        border-left: 2px solid hsl(0, 33%, 97%);
        border-collapse: collapse;
    }
}

@media screen and (min-width:960px){
    .content-container{
        margin:0 9%;
    }
}

.flex-container> h3{
    padding: 0.1em 0.2em;
    margin: 0 0 0.1em 0;
    color: rgb(109, 106, 106);
}
.flex-container>div{
    display:flex;
    flex-direction:column;
}

.flex-container.no-padding>h3{
    padding-left: calc(0.1em + 10px);
}

@media screen and (min-width:720px) {
    .flex-container{
        padding:0 10px;
    }  
    .flex-container.no-padding{
        padding:0
    }
}
.detail>p{
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: clamp(1rem, 1.5vw, 1.3rem);
    font-weight: 700;
    color:rgb(64, 64, 75);
    margin: 0;
    padding: 0.5em 0;
}
.detail>p>span{
    color: rgb(170, 170, 211);
}
.detail{
    display:grid;
    grid-template-columns: 70% 30%;
}

.btn{
    outline: none;
    margin:0.5rem 1rem;
    padding:0.5em 1.5em;
    border: none;
    cursor: pointer;
    border-radius:5px;
    font-size:clamp(1rem,1.2vw,1.2rem)
}


.btn.secondary:hover{
    background-color:rgb(211, 211, 211)
}

.btn.primary{
    background-color: rgb(60, 60, 240);
    color: white;
}

.btn.primary:hover{
    background-color: rgb(0, 0, 204);  
}

.PoNumber{
    font-size: clamp(0.9rem, 1.1vw, 1rem);
    margin:0;
    padding:0.1em 0.3em;
    padding-left: calc(0.3em + 10px);
    font-family: 'Poppins', sans-serif;
    color: rgb(162, 162, 214);
    font-weight:500;
    cursor:move;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: relative;
}
.PoNumber:hover{
    background-color:#f6f7fa
}

.PoNumber>span{
    text-align: right;
    float: right;
    display:none;
    cursor: pointer;
}

.PoNumber:hover>span{
    display:block;
}




.wheel-container-parent{
    padding:0.5rem 0;
}
.wheel-container-parent:hover{
    background-color: hsl(227, 36%, 95%);
}
.wheel-container-parent:hover>.wheel-container{
    border: 2px solid hsl(0, 3%, 70%);
}

.wheel-container {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(4, 1fr);
    height:4rem;
    border: 2px solid hsl(0, 9%, 94%);
    margin: 0 1rem;
    border-collapse: collapse;
}
.wheel-container:hover>div{
    border: 1px solid hsl(0, 7%, 89%);
    border-collapse: collapse;
}
.wheel-container>div.active{
    border: 1px solid hsl(0, 7%, 89%);
    border-collapse: collapse;
    display:flex;
    justify-content:center;
    align-items:center;
}
.wheel-container>div.active.red{
    background-color: rgb(255, 133, 133);
}
.wheel-container>div.active.blue{
    background-color: rgb(17, 120, 216);
}
.wheel-container>div.active.green{
    background-color: rgb(70, 165, 70);
}


    
.wheel-container>.div1 { grid-area: 3 / 1 / 5 / 2; }
.wheel-container>.div2 { grid-area: 3 / 2 / 5 / 3; }
.wheel-container>.div3 { grid-area: 3 / 3 / 5 / 5; }
.wheel-container>.div4 { grid-area: 3 / 5 / 5 / 7; }
.wheel-container>.div5 { grid-area: 1 / 1 / 3 / 2; }
.wheel-container>.div6 { grid-area: 1 / 2 / 3 / 3; }
.wheel-container>.div7 { grid-area: 2 / 3 / 3 / 4; }
.wheel-container>.div8 { grid-area: 2 / 4 / 3 / 5; }
.wheel-container>.div9 { grid-area: 2 / 5 / 3 / 6; }
.wheel-container>.div10 { grid-area: 2 / 6 / 3 / 7; }




.wheel {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 1fr;
    }
    
.wheel>.div1 { grid-area: 1 / 3 / 2 / 4; }
.wheel>.div2 { grid-area: 1 / 6 / 2 / 7; }
.wheel>.div3 { grid-area: 1 / 7 / 2 / 8; }


.wheel svg{
    font-size: clamp(1.3rem,1.4vw,1.5rem);
    fill: rgb(215, 212, 226);
}


.config-btns-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.config-btns-container button{
    outline:none;
    border: none;
    margin: 0 0.2rem 0.1rem 0.2rem;
    background-color: rgb(233, 233, 248);
    color: blue;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 580;
}
.config-btns-container button span{
    font-size: 1.2em;
    font-weight: 700;
}

.config-btns-container button:hover{
    background-color:blue;
    color: white;
}

.config-btns-container-parent{
    display:flex;
    flex-direction: column;
    justify-content:flex-end;
    height: 5rem;
}
